.react-form-builder-toolbar {
    overflow-y: auto;
    height: calc(100vh - 165px);
}

.builderMT {
    margin-top: 0px !important;
}

.btn-toolbar {
    /* display: none; */
}

.modal {
    background: rgba(0, 0, 0, 0.3);
}

.modal-content {
    padding: 30px;
    max-height: 800px;
    overflow-y: auto
}

.formLink {
    max-height: 30px;
    max-width: 180px;
    overflow: hidden;
}

.formDiv {
    box-shadow: 0 0.75rem 1.5rem rgba(49, 64, 71, .03);
    border-radius: 0.25rem;
    border: solid 1px;
}

.formLogo {
    text-align: center;
}

.formLogo img {
    object-fit: contain;
}